export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "ru",
    "resource": {
      "home": (ctx) => {const { normalize: _normalize } = ctx;return _normalize([""])},
      "about": (ctx) => {const { normalize: _normalize } = ctx;return _normalize([" "])},
      "projects": (ctx) => {const { normalize: _normalize } = ctx;return _normalize([""])},
      "contacts": (ctx) => {const { normalize: _normalize } = ctx;return _normalize([""])}
    }
  })
}