class Random {
  float(from = 0, to = 1): number {
    return Math.random() * (to - from) + from;
  }

  int(from = 0, to = 1): number {
    return Math.floor(this.float(from, to + 1));
  }

  string(length = 1): string {
    const chars = Array.from(
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789',
    );
    return Array.from({ length }, () => this.choice(chars)).join('');
  }

  choice<T>(array: T[]): T {
    return array[this.int(0, array.length - 1)];
  }

  boolean(chance = 0.5): boolean {
    return Math.random() < chance;
  }

  date(from: Date = new Date(0), to: Date = new Date()): Date {
    return new Date(this.int(from.getTime(), to.getTime()));
  }
}

export const random = new Random();
