import { useRouter } from 'vue-router';
import { watch } from 'vue';

export default function useDynamicTitle(sep: string) {
  const title = document.title;

  const { currentRoute } = useRouter();

  watch(
    currentRoute,
    ({ name }) =>
      (document.title = name ? title + sep + (name as string) : title),
  );
}
