import { onBeforeUnmount, onMounted, ref } from 'vue';

export default function useAutoIncrement(amount: number, period: number) {
  let interval: number;

  const inc = ref(0);

  onMounted(
    () =>
      (interval = setInterval(
        () => (inc.value = (inc.value + 1) % amount),
        period,
      )),
  );

  onBeforeUnmount(() => clearInterval(interval));

  return {
    inc,
  };
}
